import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ActivityAssignmentService {

  private assignmentActivityData = new BehaviorSubject<any>(null);
  assignmentactivitydatacast = this.assignmentActivityData.asObservable();

  private assignmentActivityStudentData = new BehaviorSubject<any>(null);
  assignmentactivitystudentdatacast = this.assignmentActivityStudentData.asObservable();

  constructor() { }

  getAssignmentActivityData(){
    return this.assignmentActivityData.value
  }

  setAssignmentActivityData(value){
    this.assignmentActivityData.next(value);
  }

  getAssignmentActivityStudentData(){
    return this.assignmentActivityStudentData.value
  }

  setAssignmentActivityStudentData(value){
    this.assignmentActivityStudentData.next(value);
  }

}
