import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ActivityAssignmentItemResolver } from './activity-assignment.resolver';

import { ActivityAssignmentComponent } from './activity-assignment.component';

const routes: Routes = [
  { path: 'list', component: ActivityAssignmentComponent},
  { path: 'list/details/:id',
    loadChildren: () => import('src/app/modules/admin/activity/activity-assignment/activity-assignment-item/activity-assignment-item.module').then(m => m.ActivityAssignmentItemModule),
    resolve: {
      ActivityAssignmentItemResolver
    }
  },
  { path: '', redirectTo: 'list', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActivityAssignmentRoutingModule { }
