import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { ActivityAssignmentService } from './activity-assignment.service';


@Injectable({
  providedIn: 'root'
})
export class ActivityAssignmentItemResolver implements Resolve<boolean> {

  constructor(
    private router: Router,
    private activityAssignmentService: ActivityAssignmentService,
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
  {

    var data = this.activityAssignmentService.getAssignmentActivityData();

    if(data == null){

      // Navigate to parent url
      this.router.navigateByUrl('/activity/assignment/list');

    }


    return;

  }

}

